/**
 * Enumeration of RTC media stream types
 * @type {{AUDIO: string, VIDEO: string}}
 */
var MediaType = {
    /**
     * The audio type.
     */
    AUDIO: "audio",
    /**
     * The video type.
     */
    VIDEO: "video"
};
module.exports = MediaType;


